.container {
  max-width: fit-content;
  margin: 20px auto;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-wrap: wrap;
}

.container2, .container3, .container4 {
  margin: 5px;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  flex: 1;
}

h1 {
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  font-family: "Poppins", sans-serif;
}

select, input[type="number"] {
  width: 300px;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex: 1 1 45%;
  font-family: "Poppins", sans-serif;
  transition: border-color 0.3s, box-shadow 0.3s;
}

select:focus, input:focus {
  border-color: #007BFF;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.25);
  outline: none;
}

@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap";

.tabs {
  list-style-type: none;
  padding: 10px;
  margin: 15px auto;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
  font-family: "Poppins", sans-serif;
}

a:link, a:visited, a:active {
  text-decoration: none;
}

.tabs a {
  padding: 10px 20px;
  text-decoration: none;
  color: black;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-right: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.tabs a:last-child {
  margin-right: 0;
}

.tabs a:hover {
  background-color: #e0e0e0;
}

.tabs a.active {
  background-color: white;
  color: black;
}

#logo {
  text-align: left;
  padding: 10px 0;
  background-color: #e9e9e9;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#logo img {
  width: 10%;
  height: 10%;
}

.field {
  display: flex;
  flex-direction: column;
}

.field label {
  margin-bottom: 5px;
  font-weight: bold;
}

.field input[type="number"], .field input[type="checkbox"] {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.checkbox-field {
  display: flex;
  align-items: center;
}

.checkbox-field input[type="checkbox"] {
  margin-right: 10px;
  width: 20px;
  height: 20px;
  appearance: none;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.checkbox-field input[type="checkbox"]:checked::before {
  content: "X";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -10%);
  font-size: 16px;
  color: #000;
}

